/* #region page */
:root {
  --color-white: #fff;
  }

.page {
  font-family: 'Cinzel', serif;
  font-size: 16px;
  line-height: 27px;
  color: #161616;
}

.page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}
/* #endregion */
/* #region header */
.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../images/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header__center {
  padding: 0 20px 288px;
}

.header__title {
  text-align: center;
  margin: 0;
  color: var(--color-white);
  font-weight: 700;
  font-size: 40px;
  line-height: 100%;
  letter-spacing: -1.5px;
}
/* #endregion */
/* #region menu */
.top-bar {
  padding: 25px 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-bar__icons {
  display: flex;
  gap: 24px;
}

.top-bar__logo {
  display: flex;
  img {
  width: 64px;
  height: 46px;
  }
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #D12D35;
}

.menu__top {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__nav {
  margin-bottom: 32px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.nav__link {
  font-weight: 700;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: var(--color-white);
}

.menu__fifth-avenue {
padding-bottom: 8px;
}

.menu__contacts-list{
  margin-bottom: 16px;
}
.menu__address {
  display: block;
  color: var(--color-white);
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}

.menu__address:hover,
.menu__phone-number:hover {
  color:#161616
}

.menu__phone-number {
  display: block;
  color: var(--color-white);
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}

.menu__call-to-order {
  display: block;
  color: var(--color-white);

  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
}

.menu__call-to-order:hover {
  padding-bottom: 7px;
  border-bottom: 1px solid var(--color-white);
  width: fit-content;
}



/* #endregion*/
/* #region icon */
.icon {
  display: block;
  width: 24px;
  height: 24px;
}

.icon--phone {
  background-image: url(../images/icons/icon-phone.png);
}

.icon--burger-menu {
  background-image: url(../images/icons/icon-burger-menu.png);
}

.icon--close{
  background-image: url(../images/icons/icon-close.png);
}
/* #endregion */

